import React from 'react';

type Props = {
  title: string;
  img?: string;
  imgAlt?: string;
  imgCaption?: string;
  description?: string;
};

const Hero = (props: Props) => {
  return (
    <section className="hero">
      <h1 className="hero-title">{props.title}</h1>
      {props.img && (
        <figure>
          <img className="hero-img" src={props.img} alt={props.imgAlt} />
          <figcaption>{props.imgCaption || ''}</figcaption>
        </figure>
      )}
      {props.description && <p className="hero-description">{props.description}</p>}
    </section>
  );
};

export default Hero;
